<template>
  <div class="search-page">
    <div class="search-wrap">
      <el-input placeholder="请输入内容" v-model="wd" autofocus>
        <template v-slot:prepend>网站</template>
        <template v-slot:append>
          <el-button icon="el-icon-search" @click="onSearch"></el-button>
        </template>
      </el-input>
    </div>
  </div>
</template>

<script>
import { routerUtils } from '@/utils/RouterUtils.js'

export default {
  name: 'Search',

  data() {
    return {
      wd: '',
    }
  },

  mounted() {},

  methods: {
    onSearch: function () {
      routerUtils.push({ path: `/searchList/${this.wd}` })
    },
  },
}
</script>

<style lang="css" scoped>
.page-header {
  height: 40px;
  line-height: 40px;
  background-color: rgb(104, 205, 241);
}
.main-page {
  width: 100%;
  height: calc(100% - 50px);
}

.search-page {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
